body {
  margin: 0;
  padding: 0;
  background-color: #3b3b3b;
  color: #fff;
}

.header {
  display: grid;
  grid-template-columns: 2fr 10fr 1fr;
  grid-template-rows: 10px 60px 60px;
  grid-template-areas:
    "begin begin begin"
    "logo searchPannel cart"
    "logo menuPanel menuPanel";
  justify-content: left;
  align-content: center;
}

.begin {
  grid-area: begin;
  background-color: #b22222;
  width: auto;
  height: auto;
}

.logo {
  grid-area: logo;
  display: flex;
  flex-direction: column;
  background-color: #000;
  width: auto;
  height: auto;
  align-content: center;
  justify-content: center;
}

.logo > div {
  text-align: center;
  margin-bottom: 5px;
  font-size: 30px;
}

.searchPannel {
  grid-area: searchPannel;
  align-self: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.searchInput {
  background-image: url('./search.png');
  background-size: 25px;
  background-position: 3px 3px;
  background-repeat: no-repeat;
  width: 1px;
  padding: 5px 30px 5px 5px;
  background-color: #f26262;
  color: #fff;
  border: 2px solid #b22222;
  font-size: 20px;
  line-height: 20px;
  transition: width 500ms ease-in-out, background-position 500ms ease-in-out;
}

.searchInput:focus, .searchInput:hover {
  width: 250px;
  background-position: 255px 3px;
}

.cartBTN {
  grid-area: cart;
  background-color: #000;
  color: #fff;
  width: auto;
  height: auto;
  font-size: 40px;
  line-height: 60px;
  text-align: center;
  text-decoration: none;
  transition: background-color 700ms;
}

.full {
  color: #32e232;
}

.mainMenu {
  grid-area: menuPanel;
  display: flex;
  flex-direction: row;
  background-color: #646464;
  align-content: center;
  justify-content: flex-start;
  border-bottom: 2px solid #fff;
}

.menuLink, .link {
  width: 90px;
  height: 58px;
  color: #fff;
  line-height: 60px;
  text-align: center;
  text-decoration: none;
  transition: background-color 700ms;
}

.link {
  border: 2px solid #fff;
}

.menuLink:hover, .link:hover, .cartBTN:hover {
  background-color: #b22222;
  transition: background-color 700ms;
}

.active {
  background-color: #b22222;
}

.notFound {
  width: 100%;
  height: 440px;
  margin: 20px auto 0;
  background-image: url('./404.jpg');
  background-position: top;
  background-repeat: no-repeat;
  background-clip: border-box;
  background-size: 100% 100%;
}

.notFound > h2 {
  font-size: 70px;
  line-height: 70px;
  margin: 0 20%;
}

.notFound > p {
  font-size: 20px;
  line-height: 20px;
  margin: 0 35%;
}

.emergencyMenu {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 280px;
  margin: 0 26%;
}

.phoneCatalog {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 15px;
}

.article {
  width: 90%;
  height: 150px;
  margin: 15px auto;
  padding: 2px 15px;
  border: 2px solid #b22222;
  background: #fff;
  color: #000;
  display: flex;
  flex-direction: row;
  box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.5), 0 6px 20px 0 rgba(255, 255, 255, 0.5);
}

.articleImage {
  height: 150px;
  width: auto;
}

.articleText {
  width: 100%;
  margin-left: 25px;
}

.articleHeader {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.buyNowBTN {
  margin: 0;
  padding: 5px;
  background-color: #b22222;
  color: #fff;
  cursor: pointer;
}

.more {
  background-color: #ffe600;
  color: #000;
}

.buyNowBTN:hover {
  background-color: #128212;
  color: #fff;
}

.phonePage {
  width: 95%;
  margin: 15px auto 0 auto;
  display: grid;
  grid-template-columns: 30% 65%;
  grid-template-rows: 60px 1fr 3fr auto;
  grid-template-areas:
    "pic model"
    "pic description"
    "pic thumbnail"
    "specification specification";
  grid-gap: 15px 5%;
}

.mainPic {
  grid-area: pic;
  display: block;
  padding: 15px;
  background-color: white;
  box-shadow: inset 0 0 15px #000;
}

.mainPic > img {
  display: block;
  margin: 0 auto;
  max-width:100%;
  max-height:100%;
}

.model {
  grid-area: model;
  color: #ccc;
}

.model > h1 {
  margin: 0;
  padding: 0;
  font-size: 45px;
  font-weight: inherit;
  line-height: 55px;
}

.devider {
  grid-area: devider;
  width: 100%;
  height: 3px;
  background-color: #b22222;
}

.description {
  grid-area: description;
  font-size: 20px;
  text-align: justify;
}

.thumbContainer {
  grid-area: thumbnail;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 45%;
  grid-gap: 5% 2%;
}

.picThumbnail {
  background-color: #fff;
  border: 4px solid #fff;
}

.picThumbnail:hover, .selected {
  border: 4px solid #b22222;
}

.picThumbnail > img {
  display: block;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
}

.specification {
  grid-area: specification;
}

.specInfo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 30px;
}

.specPart {
  width: 30%;
  box-shadow: 5px 5px 25px 3px #fff;
  padding: 5px;
}
.specPart > h4 {
  margin: 10px 0 0 0;
  padding: 0;
}

.cart {
  width: 80%;
  margin: 15px auto 0 auto;
}

.cartTable {
  border-spacing: 0 15px;
  color: #000;
}

.cartTable > thead {
  color: #fff;
}

.cartItem {
  background-color: #fff;
  box-shadow: inset 0 0 10px #000;
}

.cartTableNo {
  width: 5%;
  height: 60px;
  text-align: end;
}

.cartTablePic {
  width: 10%;
  text-align: center;
  vertical-align: top;
  padding-top: 5px;
}

.cartTableItem {
  width: 70%;
}

.cartTableQtty {
  width: 7%;
  text-align: center;
}

.cartTableDel {
  width: 8%;
  text-align: center;
  vertical-align: top;
  padding-top: 15px;
}

.cartPhoneImg {
  height: 60px;
  width: auto;
}

.cartItemName {
  text-decoration: none;
  color: #000;
}

.cartItemName:hover {
  text-decoration: underline;
  color: #00008b;
}

.cartInputQtty {
  font-size: 20px;
  line-height: 40px;
  width: 50px;
  text-align: end;
}

.cartDelItemImg {
  height: 40px;
  width: auto;
  opacity: 0.8;
  cursor: pointer;
}

.cartDelItemImg:hover {
  opacity: 1;
}



